<template>
  <div class="wrap">
    <h1><img src="../assets/logo.jpg"/></h1>
    <h3>{{ msg }}</h3>
    <a href="https://www.kancloud.cn/dsh225/javascript_vue_css/2100070" class="right-top-loading lineDocumentView" target="_blank">
      <h3>在线文档</h3>
    </a>
    <h2>基础组件</h2>
    <h4>按钮</h4>
    <dm-button>普通</dm-button>
    <dm-button type="primary" @click="btnClick" icon="dm__icon--checked" >主要</dm-button>
    <dm-button type="error" icon="dm__icon--checked" ></dm-button>
    <dm-button type="warning" disabled>禁止</dm-button>
    <dm-button plain>简约</dm-button>
    <dm-button type="text" icon="dm__icon--checked">文字</dm-button>
    <dm-button loading type="success">loading状态</dm-button>
    <dm-button bg-color="#fc0" color="#e0439a" border-color="#fc0">自定义颜色</dm-button>
    <br/><br/>
    <dm-button bg-color="#fcb" color="#e0439a" border-color="#fcb" block no-radius class="btn__block">无圆角块状</dm-button>
    <br/>
    <dm-button round long>100%宽圆角</dm-button>
    <br/><br/>
    <dm-button-group class="btn__group">
      <dm-button type="warning">警告</dm-button>
      <dm-button type="primary" @click="btnClick" icon="dm__icon--checked" >主要</dm-button>
      <dm-button type="success" icon="dm__icon--loading">成功</dm-button>
    </dm-button-group>
    

    <h4>标签</h4>
    <dm-tag>普通</dm-tag>
    <dm-tag type="primary">primary</dm-tag>
    <dm-tag type="success">success</dm-tag>
    <dm-tag type="warning" round>warning</dm-tag>
    <dm-tag type="error" round>error</dm-tag>
    <dm-tag bg-color="#fc0" color="#e0439a" border-color="#fc0">自定义颜色</dm-tag>
    <dm-tag type="success" v-if="tag1" closable @close="tag1=false">success</dm-tag>

    <h4>搜索框</h4>
    <p style="text-align:left;font-size: 14px;">input方法监听的值：{{inputValue}}</p>
    <form action="/"><dm-search bg-color="#09c" @action="searchAction" v-model="value" action-text-color="#fff"><span slot="search">确定</span></dm-search></form>
    <dm-search placeholder="自定义placeholder" v-model="value"  @action="searchAction" @input="inputAction" action-text-color="#777" cancel-text-color="#777"  @cancel="searchCancel"><span slot="search">搜索</span><span slot="cancel">取消</span></dm-search>


    <h4>加载更多</h4>
    <dm-loadmore @action="loadMore"></dm-loadmore>
    <dm-loadmore icon color="#2d8cf0">loading...</dm-loadmore>
    <dm-loadmore no-data>暂无数据</dm-loadmore>
    <dm-loadmore no-data>▪</dm-loadmore>


    <h4>表单</h4>
    <div class="form-demo">
      <dm-cell-group>
        <!-- <div slot="top">头部的</div> -->
        <dm-cell-item>
          <span slot="left">用户名：</span>
          <dm-input slot="right" v-model="value1" name="name1" max="10" placeholder="请输入用户名" @blur="inputAction1" @focus="inputAction2" has-close></dm-input>
        </dm-cell-item>
        <dm-cell-item>
          <span slot="left">密  码：</span>
          <dm-input slot="right" type="password" v-model="value1" placeholder="请输入密码" has-close></dm-input>
        </dm-cell-item>
        <dm-cell-item>
          <span slot="leftIcon" class="dm__icon--phone"></span>
          <dm-input slot="right" type="tel" v-model="value1" placeholder="请输入手机号"></dm-input>
          <dm-button slot="right" type="error" class="btn__block" style="padding: 6px;width: 120px;font-size: 12px;">获取验证码</dm-button>
        </dm-cell-item>
        <dm-cell-item>
          <dm-input slot="right" :value="value1" placeholder="这里是readonly" readonly has-close></dm-input>
          <span slot="rightIcon" class="dm__icon--delete"></span>
        </dm-cell-item>
        <dm-cell-item>
          <dm-input slot="right" :value="value1" placeholder="这里是disabled"   disabled></dm-input>
          <span slot="rightIcon" class="dm__icon--delete"></span>
        </dm-cell-item>
        <dm-cell-item type='link' href="tel:400517517">
          <span slot="leftIcon" class="dm__icon--contact"></span>
          <span slot="left">联系方式</span>
          <span slot="right">400517517</span>
          <span slot="rightIcon" class="dm__icon--right"></span>
        </dm-cell-item>
        <dm-cell-item @click="cellClick">
          <span slot="left">我的任务</span>
          <dm-tag slot="right" type="error" round style="margin-bottom:0;">8</dm-tag>
          <span slot="rightIcon" class="dm__icon--right"></span>
        </dm-cell-item>
        <dm-cell-item @click="cellClick">
          <span slot="left">我的消息</span>
          <span slot="right" class="dot"></span>
          <span slot="rightIcon" class="dm__icon--right"></span>
        </dm-cell-item>
        <dm-cell-item>
          <dm-textarea slot="right" placeholder="请输入留言，50字以内" :value="value" @input="textareaAction" max="50"></dm-textarea>
        </dm-cell-item>
        <dm-cell-item>
          <dm-select slot="right" :data="selectList" @change="selectAction" v-model="curSelected"></dm-select>
          <span slot="rightIcon" class="dm__icon--right"></span>
        </dm-cell-item>
        <dm-cell-item>
          <span slot="left">出行方式</span>
          <dm-select slot="right" right :data="selectList" v-model="curSelected"></dm-select>
          <span slot="rightIcon" class="dm__icon--right"></span>
        </dm-cell-item>
        <dm-flexbox>
          <div style="width:30%"><dm-button block no-radius long type="default" style="margin-top:10px">取消</dm-button></div>
          <dm-flexbox-item><dm-button block no-radius long type="success" style="margin-top:10px">提交</dm-button></dm-flexbox-item>
        </dm-flexbox>
      </dm-cell-group>

      <dm-cell-group>
        <dm-cell-item>
          <span slot="left">不让他看我的朋友圈</span>
          <dm-switch slot="right" :value="switch1" @input="switchAction"></dm-switch>
        </dm-cell-item>
        <dm-cell-item>
          <span slot="left">不看他的朋友圈</span>
          <dm-switch slot="right" :value="switch2" @input="switchAction"></dm-switch>
        </dm-cell-item>
      </dm-cell-group>

      <dm-cell-group title="单选" class="dm-radio-group">
        <dm-cell-item>
          <dm-radio-group slot="right" v-model="radio" color="#4cd864">
            <dm-radio value="巴士"></dm-radio>
            <dm-radio value="火车"></dm-radio>
            <dm-radio value="飞机" disabled>飞机--disabled</dm-radio>
            <dm-radio value="单车">单车</dm-radio>
          </dm-radio-group>
        </dm-cell-item>
      </dm-cell-group>

      <dm-cell-group title="单选" class="dm-radio-group" style="background: #fff;margin-bottom: 20px;text-align: center;">
        <dm-cell-item>
          <dm-radio-group slot="right" v-model="radio" color="#ed3f14" hor>
            <dm-radio value="巴士"></dm-radio>
            <dm-radio value="火车"></dm-radio>
            <dm-radio value="飞机" disabled>飞机--disabled</dm-radio>
            <dm-radio value="单车">单车</dm-radio>
          </dm-radio-group>
        </dm-cell-item>
      </dm-cell-group>
  

      <dm-cell-group title="复选" class="dm-checkbox-group" >
        <dm-cell-item>
          <dm-checkbox-group slot="right" v-model="checkbox" color="#4cd864">
            <dm-checkbox value="1">巴士</dm-checkbox>
            <dm-checkbox value="2">火车</dm-checkbox>
            <dm-checkbox value="3" disabled>飞机--disabled</dm-checkbox>
            <dm-checkbox value="4">单车</dm-checkbox>
          </dm-checkbox-group>
        </dm-cell-item>
      </dm-cell-group>
      
      

      <dm-cell-group>
        <div slot="bottom" style="overflow: hidden;word-break: break-all;text-align: left;padding-left: 10px;font-size: 14px;">
          <p>您输入：{{inputVal}}</p>
          <p>留言：{{textareaVal}}</p>
          <p>出行方式：{{selectVal}}</p>
          <p>朋友圈：{{switchVal}}</p>
          <p>单选：{{radio}}</p>
          <p>复选：{{checkbox}}</p>
        </div>
      </dm-cell-group>

    </div>


    <h4>网格和图标</h4>
    <dm-grids row="6">
      <dm-grid href="https://github.com/dsh225/DMUI">
