    <h4>flex布局</h4>
    <dm-flexbox class="flex">
      <div>普通div</div>
      <dm-flexbox-item @click='modalClick1'>默认的水平flex div</dm-flexbox-item>
      <div>普通div</div>
    </dm-flexbox>
    <br>
    <dm-flexbox direction="vertical" class="flex">
      <div>普通div</div>
      <dm-flexbox-item>direction="vertical" 的垂直flex div</dm-flexbox-item>
      <div>普通div</div>
    </dm-flexbox>




    <h4>导航栏(顶部)</h4>
    <div class="navbar-wrap">
      <dm-navbar title="首页文字很长很长很长啊啊啊啊啊" bgcolor='#fff' color="#333" left-text="返回" left-icon="dm__icon--left" right-text="设置" @left-action="clickLeft" @right-action="clickRight"></dm-navbar>
      <br>
      <dm-navbar bgcolor='#fff' color="#333" left-text="" left-icon="dm__icon--left" right-icon="dm__icon--edit" @left-action="clickLeft" @right-action="clickRight"><img src="http://chuantu.xyz/t6/741/1610937103x2073530471.jpg"></dm-navbar>
      <dm-navbar bgcolor='#fff' color="#333" left-text="" left-icon="dm__icon--left" right-icon="dm__icon--edit" @left-action="clickLeft" @right-action="clickRight" ><div><span slot="icon" class="dm__icon--loading"></span>这里是HTML</div></dm-navbar>
    </div>



    <h4>标签栏(底部)</h4>
    <div class="tabbar-wrap">
      <dm-tabbar v-model="active">
        <dm-tabbar-item icon="dm__icon--home" type="link" href="https://github.com/dsh225/DMUI" dot>首页</dm-tabbar-item>
        <dm-tabbar-item icon="dm__icon--mall" :href="hrefObj" badge="8" @click="routerAction"><span slot="badge">8</span>商城</dm-tabbar-item>
        <dm-tabbar-item icon="dm__icon--add" href="add" class="add"></dm-tabbar-item>
        <dm-tabbar-item href="###" img-src="http://chuantu.xyz/t6/741/1610937103x2073530471.jpg">自定义图</dm-tabbar-item>
        <dm-tabbar-item icon="dm__icon--my" href="my" badge="11">我的</dm-tabbar-item>
      </dm-tabbar>
    </div>



    <h4>Tab</h4>




    <h2>弹出层</h2>
    <h4>Modal</h4>
    <dm-button type="success" @click="modalClick1">alert</dm-button>
    <dm-button type="primary" @click="modalClick2">自定义内容confirm</dm-button>
    <dm-button type="error" @click="modalClick3">特殊应用层</dm-button>
    <br>
    <dm-button type="success" @click="modalClick4">全局alert层</dm-button>
    <dm-button type="primary" @click="modalClick5">全局confirm层,带回调函数</dm-button>
    <dm-button type="warning" @click="modalClick6">全局prompt层,带回调函数</dm-button>

    <dm-modal type="alert" :visible="modalVisible1" @close="modalClose1" @confirm="modalOk1" dialog-title="提示" confirm-text='自定义文字' color="#fff" :z-index="zIndex" :mask-closable="false" >点击背景功能关闭</dm-modal>
    <dm-modal type="confirm" dialog-title="填写资料" :visible="modalVisible2" @close="modalClose2" @confirm="modalOk2" confirm-text='ok啦' 
        cancel-text='窝的confirm-btn禁止了' color="#19be6b" :z-index="zIndex" :confirm-btn="false">
      <dm-input v-model="value2" name="name2" max="10" placeholder="请输入用户名" @blur="inputAction1" @focus="inputAction2"></dm-input>
      <dm-textarea placeholder="请输入留言，50字以内" :value="value3" @input="textareaAction3" max="50"></dm-textarea>
    </dm-modal>
    <dm-sp-modal :visible="modalVisible3" @close="modalClose3" :z-index="zIndex"><img src="https://m.360buyimg.com/n12/s750x750_jfs/t13243/363/119511899/34477/f555b966/5a03ffafNd99ceef4.jpg"/></dm-sp-modal>


    <h4>Toast</h4>
    <dm-button type="success" @click="toastClick1">文字</dm-button>
    <dm-button type="primary" @click="toastClick2">loading</dm-button>
    <dm-button type="error" @click="toastClick3">无遮罩</dm-button>



    <h4>Loading</h4>
    <dm-loading></dm-loading>
    <!-- 自定义定位右上角 -->
    <dm-loading class="right-top-loading" width="20" height="20" color="#ed3f14"></dm-loading>
    <dm-loading has-text>自定义文字</dm-loading>
    <!-- 测试开启 -->
    <!-- <dm-loading has-text fullScreen closable @close="loadingStatus=false" v-show="loadingStatus">全屏可关闭的loading</dm-loading> -->
    <dm-loading has-text vertical border-width="8">垂直的loading</dm-loading>
    <dm-loading ><div slot="cus"><img style="width:40px" src="http://chuantu.xyz/t6/741/1610937103x2073530471.jpg"><p style="color:#19be6b;margin:0;">自定义效果</p></div></dm-loading>



    <h4>ActionSheet</h4>
    <dm-button type="success" @click="actionSheetVisible1=true">有取消</dm-button>
    <dm-button type="primary" @click="actionSheetVisible2=true">无取消</dm-button>

    <dm-actionsheet :item-list="itemList" cancel="取消" cancel-color="#19be6b" v-model="actionSheetVisible1" header="这里是个标题" has-icon></dm-actionsheet>
    <dm-actionsheet :item-list="itemList" v-model="actionSheetVisible2" has-icon></dm-actionsheet>




    <h4>无缝滚动</h4>
    <div class="seamlessscroll">
      <dm-seamlessscroll autoplay="2000">
        <dm-seamlessscroll-item @click.native="modalClick1">只有一条的时候不滚动，默认向上滚动</dm-seamlessscroll-item>
      </dm-seamlessscroll>
    </div>
    <div class="seamlessscroll">
      <dm-seamlessscroll autoplay="3000" direction="down">
        <dm-seamlessscroll-item>邵逸夫奖名单公布</dm-seamlessscroll-item>
        <dm-seamlessscroll-item @click.native="modalClick1">女星玛戈基德去世</dm-seamlessscroll-item>
        <dm-seamlessscroll-item>多国谴责美搬使馆</dm-seamlessscroll-item>
      </dm-seamlessscroll>
    </div>



    <h4>Popup</h4>
    <dm-button type="success" @click="popupVisible1=true">弹出popup</dm-button>

    <dm-popup v-model="popupVisible1" header="这里是个标题" cancel="取消" cancel-color="#19be6b">
      <dm-cell-group class="selectSign">
        <dm-cell-item>
          <img src="http://chuantu.xyz/t6/741/1610937103x2073530471.jpg" slot="left" class="img">
          <div class="" slot="right"  @click="openModalBox">
            <p>电子提案 <dm-tag bg-color="#fff" color="#ed3f14" border-color="#ed3f14" round>荐</dm-tag></p>
            <p>通过在网上进行电子提案</p>
          </div>
        </dm-cell-item>
        <dm-cell-item>
          <img src="http://chuantu.xyz/t6/741/1610937103x2073530471.jpg" slot="left" class="img">
          <div class="" slot="right">
            <p>原始提案</p>
            <p>采用原始方式进行提案，平台只进行记录</p>
          </div>
        </dm-cell-item>
      </dm-cell-group>
    </dm-popup>



    <h4>数字键盘</h4>
    <dm-cell-group>
      <dm-cell-item>
        <span slot="left">房间面积</span>
        <div slot="right" class="dm__cell-right" @click="openCurKeyboard" data-tips='{"title":"房间面积", "placeholder":"请输入房间面积", "name":"roomAreaVal", "unit":"㎡", "isCard":false}' :data-val="roomAreaVal">
          <dm-input v-model="roomAreaVal" placeholder="㎡" disabled="disabled" ></dm-input>
        </div>
      </dm-cell-item>
      <dm-cell-item>
        <span slot="left">房间租金</span>
        <div slot="right" class="dm__cell-right" @click="openCurKeyboard" data-tips='{"title":"房间租金", "placeholder":"请输入房间租金", "name":"roomRentVal", "unit":"元/月", "isCard":false}' :data-val="roomRentVal">
          <dm-input v-model="roomRentVal" placeholder="元/月" disabled="disabled" ></dm-input>
        </div>
      </dm-cell-item>
      <dm-cell-item>
        <span slot="left">身份证号码</span>
        <div slot="right" class="dm__cell-right" @click="openCurKeyboard" data-tips='{"title":"身份证号码", "placeholder":"请输入身份证号码", "name":"idCardVal", "unit":"这里无效", "isCard":true}' :data-val="idCardVal">
          <dm-input v-model="idCardVal" placeholder="请输入身份证号码" disabled="disabled" ></dm-input>
        </div>
      </dm-cell-item>
    </dm-cell-group>
    <dm-keyboard :visible="keyBoardVisible" v-model="curVal" :tips="tips" :len="vLen" @confirm="save" @close="keyBoardClose"></dm-keyboard>


    <h4>表格</h4>
    <dm-table :columns="columns" :tableData="tableData" :seqNumber="true" :mutiSelect="true" :operatesShow="true" :operates="operates"></dm-table>
    
    <h4>进度条</h4>
    <div class="progressView">
      <dm-progress :percentage="50"></dm-progress>
      <dm-progress :text-inside="true" :stroke-width="24" :percentage="100" status="success"></dm-progress>   
      <dm-progress :percentage="percentage" :color="customColor"></dm-progress>
      <dm-progress :percentage="percentage" :color="customColorMethod"></dm-progress>
      <dm-progress :percentage="percentage" :color="customColors"></dm-progress>
      <div class="text-al-l">
        <div>
          <dm-progress type="dashboard" :percentage="percentage" :color="customColorMethod"></dm-progress>
        </div>
        <dm-button plain icon="dm__icon--reduce-outline" @click="decrease"></dm-button>
        <dm-button plain icon="dm__icon--add-outline" @click="increase"></dm-button>
      </div>
    </div>
    
    <h4>轮播</h4>
    <div class="block">
      <span class="demonstration">默认 Hover 指示器触发</span>
      <dm-carousel height="150px">
        <dm-carousel-item v-for="item in 4" :key="item">
          <h3 class="small">{{ item }}</h3>
        </dm-carousel-item>
      </dm-carousel>
    </div>
    <div class="block">
      <span class="demonstration">Click 指示器触发</span>
      <dm-carousel trigger="click" height="150px">
        <dm-carousel-item v-for="item in 4" :key="item">
          <h3 class="small">{{ item }}</h3>
        </dm-carousel-item>
      </dm-carousel>
    </div>
    <div class="block">
      <span class="demonstration">卡片化</span>
      <dm-carousel :interval="4000" type="card" height="200px">
        <dm-carousel-item v-for="item in 6" :key="item">
          <h3 class="medium">{{ item }}</h3>
        </dm-carousel-item>
      </dm-carousel>
    </div>
    <h4>日期选择器</h4>
    <div class="block">
      <span class="demonstration">默认</span>
      <div class="borderColor-e5e5e5 dmDatePickerView">
        <dm-date-picker
          v-model="dateValue1"
          type="date"
          placeholder="选择日期">
        </dm-date-picker>
      </div>
    </div>
    <div class="block">
      <span class="demonstration">带快捷选项</span>
      <div class="borderColor-e5e5e5 dmDatePickerView">
        <dm-date-picker
          v-model="dateValue2"
          align="right"
          type="date"
          placeholder="选择日期"
          :picker-options="pickerOptions">
        </dm-date-picker>
      </div>
    </div>
    <div class="block">
      <span class="demonstration">月</span>
      <div class="borderColor-e5e5e5 dmDatePickerView">
        <dm-date-picker
          v-model="dateValue3"
          type="month"
          placeholder="选择月">
        </dm-date-picker>
      </div>
    </div>
    <div class="block">
      <span class="demonstration">年</span>
      <div class="borderColor-e5e5e5 dmDatePickerView">
        <dm-date-picker
          v-model="dateValue4"
          type="year"
          placeholder="选择年">
        </dm-date-picker>
      </div>
    </div>
    <div class="block">
      <span class="demonstration">选择日期范围(带快捷选项)</span>
      <div class="borderColor-e5e5e5 dmDatePickerView">
        <dm-date-picker
          v-model="dateValue5"
          type="daterange"
          align="right"
          unlink-panels
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          :picker-options="pickerOptions2">
        </dm-date-picker>
      </div>
    </div>

    <h2>应用组件</h2>

    <h4>工单流程</h4>
    <dm-step :step="step" :step-list="stepList"></dm-step>


    <h4>Skeleton骨架</h4>
    <dm-skeleton type="circle" @click.native="modalClick1"></dm-skeleton>
    <dm-skeleton  animate="loading"></dm-skeleton>

    <div class="topic-loading-item">
      <div class="loading-item-one">
        <dm-skeleton type="circle"></dm-skeleton>
        <dm-skeleton width="40%"></dm-skeleton>
      </div>
      <div class="loading-item-two">
        <dm-skeleton width="80%"></dm-skeleton>
      </div>
      <div class="loading-item-three">
        <dm-skeleton width="60%"></dm-skeleton>
      </div>
    </div>
    


    <h4>Lazy延迟加载</h4>
    <dm-lazy :time="4000" @loaded="loadedAction">
      <img style="width:50%" src="../assets/dq.jpeg"/>
      <div class="topic-loading-item" slot="skeleton">
        <div class="loading-item-two">
          <dm-skeleton width="100%" animate="loading"/>
        </div>
        <div class="loading-item-three">
          <dm-skeleton width="100%" animate="loading"/>
        </div>
      </div>
      <!-- <dm-loading width="20" height="20" color="#ed3f14" slot="loading"></dm-loading> -->
    </dm-lazy>

    <div @click="goTop" class="goTopView" v-show="showGoTop">
      <span slot="icon" class="dm__icon--totop" title="返回顶部"></span>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      msg: `DMUI - 基于vue2.x，可复用UI组件`,
      value: '',
      value1: '',
      value2: '',
      value3: '',
      dateValue1:'',
      dateValue2:'',
      dateValue3:'',
      dateValue4:'',
      dateValue5:'',
      inputValue: '',
      modalVisible1: false,
      modalVisible2: false,
      tableData: [],
      rateValue1: null,
      rateValue2: null,
      pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
      },
      pickerOptions2: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      rateColors: ['#99A9BF', '#F7BA2A', '#FF9900'],
      modalVisible3: false,
      columns: [
            {
                label: "name",
                prop: "name",
            },
            {
                label: "sex",
                prop:  "sex",
            },
            {
                label: "age",
                prop:  "age",
            },
            {
                label: "address",
                prop:  "address",
            },
      ],
      operates: {
          width: 210,
          fixed: '',
          align: 'center',
          list: [
              {
                  id: '1',
                  label: '新增',
                  icon: 'el-icon-edit',
                  show: true,
                  disabled: false,
                  status:'primary',
                  method: (row, index) => {
                      this.tableAdd(row, index);
                  },
              },
              {
                  id: '2',
                  label: '编辑',
                  icon: 'el-icon-edit',
                  show: true,
                  disabled: false,
                  status:'warning',
                  method: (row, index) => {
                      this.tableEdit(row, index)
                  },
              },
              {
                  id: '3',
                  label: '删除',
                  icon: 'el-icon-edit',
                  show: true,
                  disabled: false,
                  status:'danger',
                  method: (row, index) => {
                      this.tableDel(row, index)
                  },
              },
          ]
      }, // 列操作按钮
      zIndex: 300000,
      stepList: ['发起工单', '主管审批', '经理审批', '总监核查', '结束'],
      step: 2,
      tag1: true,
      switch1: true,
      switch2: false,
      selectList: [
                    { name: '请选择出行方式', value: 0 },
                    { name: '巴士', value: 1 },
                    { name: '快车', value: 2 },
                    { name: '专车', value: 3 },
                    { name: '顺风车', value: 4 },
                    { name: '出租车', value: 5 },
                    { name: '代驾', value: 6 }
      ],
      curSelected: '3',
      inputVal: '',
      textareaVal: '',
      selectVal: '',
      switchVal: true,
      showGoTop:false,
      gridRow: [
                    { icon: '请选择出行方式', text: '巴士' },
                    { name: '巴士', text: '巴士' },
                    { name: '快车', text: '快车' },
                    { name: '专车', text: '专车' },
                    { name: '顺风车', text: '顺风车' },
                    { name: '出租车', text: '出租车' },
                    { name: '代驾', text: '代驾' }
      ],
      radio: '巴士',
      percentage: 20,
      customColor: '#409eff',
      customColors: [
        {color: '#f56c6c', percentage: 20},
        {color: '#e6a23c', percentage: 40},
        {color: '#5cb87a', percentage: 60},
        {color: '#1989fa', percentage: 80},
        {color: '#6f7ad3', percentage: 100}
      ],
      checkbox: ['1', '4'],
      hrefObj: { path: '/mall', params: { userId: 123 } },
      itemList: [
        { text: '顺风车',
          icon: '<span class="dm__icon--loading"></span>',
          callBack: () => {
            this.$modal.alert({
              title: '提示',
              content: '我是actionsheet弹出来的',
              color: '#19be6b'
            })
          }
        },
        { text: '巴士', icon: '<img src="http://chuantu.xyz/t6/741/1610937103x2073530471.jpg"/>', callBack: () => {} },
        { text: '快车',
          icon: '<p>“别打算XSS攻击”</p>',
          callBack: () => {
            this.$modal.alert({
              title: '提示',
              content: '想了解vue XSS攻击点确定',
              color: '#19be6b',
              callBack: () => {
                location.href = 'https://segmentfault.com/q/1010000009844447'
              }
            })
          }
        },
        { text: '专车', callBack: () => {} },
        { text: '',
          icon: '<p style="color:red"><span class="dm__icon--emoji"></span>单车</p>',
          callBack: () => {}
        }
      ],
      actionSheetVisible1: false,
      actionSheetVisible2: false,
      popupVisible1: false,
      popupVisible2: false,
      active: '/mall',
      loadingStatus: true,

      keyBoardVisible: false, // 是否显示数字键盘
      tips: {}, // 键盘输入框配置项
      curVal: '', // 键盘传入值
      vLen: 8,
      roomAreaVal: '', // 房间面积
      roomRentVal: '', // 房间租金
      idCardVal: '',
      movieList: [
            {
                name: 'The Shawshank Redemption',
                url: 'https://movie.douban.com/subject/1292052/',
                rate: 9.6
            },
            {
                name: 'Leon:The Professional',
                url: 'https://movie.douban.com/subject/1295644/',
                rate: 9.4
            },
            {
                name: 'Farewell to My Concubine',
                url: 'https://movie.douban.com/subject/1291546/',
                rate: 9.5
            },
            {
                name: 'Forrest Gump',
                url: 'https://movie.douban.com/subject/1292720/',
                rate: 9.4
            },
            {
                name: 'Life Is Beautiful',
                url: 'https://movie.douban.com/subject/1292063/',
                rate: 9.5
            },
            {
                name: 'Spirited Away',
                url: 'https://movie.douban.com/subject/1291561/',
                rate: 9.2
            },
            {
                name: 'Schindlers List',
                url: 'https://movie.douban.com/subject/1295124/',
                rate: 9.4
            },
            {
                name: 'The Legend of 1900',
                url: 'https://movie.douban.com/subject/1292001/',
                rate: 9.2
            },
            {
                name: 'WALL·E',
                url: 'https://movie.douban.com/subject/2131459/',
                rate: 9.3
            },
            {
                name: 'Inception',
                url: 'https://movie.douban.com/subject/3541415/',
                rate: 9.2
            }
        ],
        randomMovieList: []
    }
  },
  methods: {
    clickLeft () {
      alert('左边')
    },
    clickRight () {
      alert('右边')
    },
    routerAction () {

    },
    changeLimit () {
      function getArrayItems(arr, num) {
          const temp_array = [];
          for (let index in arr) {
              temp_array.push(arr[index]);
          }
          const return_array = [];
          for (let i = 0; i<num; i++) {
              if (temp_array.length>0) {
                  const arrIndex = Math.floor(Math.random()*temp_array.length);
                  return_array[i] = temp_array[arrIndex];
                  temp_array.splice(arrIndex, 1);
              } else {
                  break;
              }
          }
          return return_array;
      }
      this.randomMovieList = getArrayItems(this.movieList, 5);
    },
    cellClick () {
      this.$toast.text({content: '您有8条消息，注意查收', direction: 'top'})
    },
    customColorMethod(percentage) {
      if (percentage < 30) {
        return '#909399';
      } else if (percentage < 70) {
        return '#e6a23c';
      } else {
        return '#67c23a';
      }
    },
    increase() {
      this.percentage += 10;
      if (this.percentage > 100) {
        this.percentage = 100;
      }
    },
    decrease() {
      this.percentage -= 10;
      if (this.percentage < 0) {
        this.percentage = 0;
      }
    },
    gridClick () {
      alert(1)
    },
    switchAction (val) {
      this.switchVal = val
    },
    inputAction2 () {

    },
    progressFormat(percentage) {
        return percentage === 100 ? '满' : `${percentage}%`;
    },
    handleScroll () {
      let that = this
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop
      that.scrollTop = scrollTop
      if(that.scrollTop > 200) {
        this.showGoTop = true;
      } else {
        this.showGoTop = false;
      }
    },
    // 返回顶部
     goTop () {
      // window.scroll(0,0);
      let that = this
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 10)
        document.documentElement.scrollTop = document.body.scrollTop =
          that.scrollTop + ispeed
        if (that.scrollTop === 0) {
          clearInterval(timer)
        }
      }, 10)
    },
    inputAction1 (val) {
      this.inputVal = val
    },
    selectAction (val) {
      this.selectVal = val
    },
    textareaAction (val) {
      this.textareaVal = val
    },
    textareaAction3 (val) {
      this.value3 = val
    },
    toastClick1 () {
      this.$toast.text({
        content: '太长会换行呢，15个字以内最好',
        direction: 'bottom',
        callBack () {
          setTimeout(() => {
            this.$toast.text({content: '关闭后回调操作', direction: 'bottom'})
          }, 2000)
        }
      })
    },
    toastClick2 () {
      this.$toast.loading({
        // mask: false
      })
    },
    toastClick3 () {
      this.$toast.loading({
        mask: false
      })
    },
    modalClick4 () {
      this.$modal.alert({
        title: '这里可以自定义',
        content: '开启3秒关闭，取消背景层关闭',
        confirmText: '窝要关了',
        color: '#f9f9f9',
        autoClose: true,
        maskClosable: false
      })
    },
    modalClick5 () {
      this.$modal.confirm({
        title: '提示',
        content: '你想怎么样呢？',
        confirmText: 'ok啦',
        cancelText: '窝要关了',
        callBack () {
          this.$modal.alert({
            title: '这里可以自定义',
            content: '开启3秒关闭，取消背景层关闭',
            confirmText: '知道了',
            autoClose: true,
            maskClosable: false
          })
        },
        closeAction () {
          this.$toast.text({content: '触发关闭回调函数', direction: 'bottom'})
        }
      })
    },
    modalClick6 () {
      this.$modal.prompt({
        title: '你想怎么样呢？',
        placeholder: '填入您的支付宝密码',
        confirmText: 'ok啦',
        cancelText: '窝要关了',
        // readonly: true,
        callBack (val) {
          this.$modal.alert({
            title: '我爱你',
            content: `密码是${val}，我爱你真的，把帐号也发我一遍 `,
            confirmText: 'ok的啦'
          })
        }
      })
    },
    btnClick () {
      alert('按钮点击')
    },
    closeTag () {
      this.tag1 = false
    },
    loadMore () {
      alert(`loadMore`)
    },
    searchAction (e) {
      this.value = e
      alert(this.value)
    },
    searchCancel (e) {
      this.value = ''
    },
    inputAction (e) {
      this.inputValue = e
    },
    modalClick1 (e) {
      this.modalVisible1 = true
      this.zIndex++
    },
    modalClick2 (e) {
      this.modalVisible2 = true
      this.zIndex++
    },
    modalClick3 (e) {
      this.modalVisible3 = true
      this.zIndex++
    },
    modalClose1 (e) {
      this.modalVisible1 = false
    },
    modalClose2 (e) {
      this.modalVisible2 = false
    },
    modalClose3 (e) {
      this.modalVisible3 = false
    },
    modalOk1 (e) {
      this.modalVisible1 = false
    },
    modalOk2 (e) {
      alert(`${this.value2} 和 ${this.value3}`)
      this.modalVisible2 = false
      this.value2 = ''
      this.value3 = ''
    },
    openModalBox (e) {
      this.popupVisible1 = false
      this.$modal.alert({
        title: '这里可以自定义',
        content: '开启3秒关闭，取消背景层关闭',
        autoClose: true,
        maskClosable: false
      })
    },
    tableAdd(row, index) {
      this.zIndex++
      this.popupVisible1 = false
      this.$modal.alert({
        title: '这里可以自定义',
        content: '开启3秒关闭，取消背景层关闭',
        autoClose: true,
        maskClosable: false
      })
    },
    tableEdit(row, index) {
      this.zIndex++
      this.popupVisible1 = false
      this.$modal.alert({
        title: '编辑第' + index + '条数据',
        content: row,
        autoClose: true,
        maskClosable: false
      })
    },
    tableDel(row, index) {
      this.zIndex++
      this.popupVisible1 = false
      this.$modal.alert({
        title: '删除第' + index + '条数据',
        content: row,
        autoClose: true,
        maskClosable: false
      })
    },
    loadedAction () {
      this.$toast.text({content: '延迟加载完成！', direction: 'bottom'})
    },
    // 显示数字键盘
    openCurKeyboard (e) {
      const obj = JSON.parse(e.currentTarget.dataset.tips)
      this.curVal = e.currentTarget.dataset.val
      this.keyBoardVisible = true
      this.tips = {title: obj.title, placeholder: obj.placeholder, name: obj.name, unit: obj.unit, isCard: obj.isCard}
    },
    // 确定
    save (data) {
      this.keyBoardVisible = false
      if (data.val && data.val !== '') {
        this[[data.name]] = data.val + data.unit
      } else {
        this[[data.name]] = ''
      }
    },
    keyBoardClose () {
      this.keyBoardVisible = false
    },
    mockList (size) {
      return new Promise(resolve => {
        const list = []
        for (let index = 0; index < size; index++) {
          list.push({
            name: `名称${index}`,
            sex: '0',
            num: 123,
            age: 18,
            num2: 234,
            rate: 3,
            address: 'shenzhen'
          })
        }
        resolve(list)
      })
    }
  },
  created() {
    this.mockList(500).then(data => {
      this.tableData = data
    })
  },
  destroyed () {
    document.removeEventListener("scroll", this.handleScroll);
  },
  mounted () {
    window.addEventListener("scroll", this.handleScroll);
    this.changeLimit();
  }
}
</script>

<style lang="scss">
#demo-wrap{
  margin: 10px auto;
}
.wrap{
  margin: 10px;
}
h1,h3{
  margin: 0;
}
h3{
  font-weight: normal;
  font-size: 16px;
}
h4{
  font-weight: normal;
  font-size: 14px;
  text-align: left;
  background-color: #e9e9e9;
  padding: 10px;
}
h2{
  text-align: left;
  width: 100%;
  border-bottom: 1px dashed #ededed;
  font-size: 16px;
  height: 40px;
  line-height: 40px;
  text-indent: 10px;
  margin-top: 30px;
  margin-bottom: 0;
}
.lineDocumentView {
  h3{
    text-align: left;
    width: 100%;
    font-size: 16px;
    // height: 40px;
    // line-height: 40px;
    // text-indent: 10px;
    margin-top: 30px;
    margin-bottom: 0;
    font-weight: bold;
  }
}
.btn__group{
  border: 1px solid #eee;
  padding: 10px;
  position: relative;
  margin: 0 20px;
}
.btn__group::before{
  content: '按钮组';
  font-size:12px;
  position: absolute;
  top: -6px;
  right: -20px;
  background: #fff;
  color: #aaa;
}
.btn__block{
  margin: 0 auto!important;
}
.wrap > .dm__btn,.dm__tag{
  margin-bottom: 10px;
}
.dm__dialog--special .dm__dialog--bd img{
  width: 100%;
  border-radius: 4px;
}
.dm__loading--wrap{
  margin: 15px auto;
}
.right-top-loading{
  position: fixed;
  right: 5px;
  top: 5px;
  z-index: 9999;
}
.right-top-loading.dm__loading--wrap{
  margin: 0;
}
.form-demo{
  width: 98%;
  margin: 0 auto;
  background: #f5f5f5;
  padding: 1%;
}
// 骨架demo
.topic-loading-item{
  margin-top: 20px;
}
.topic-loading-item .loading-item-one{
  display: flex;
  align-items: center;
}
.topic-loading-item .loading-item-one .dm__skeleton--bar{
  margin-left: 10px;
}
.topic-loading-item .loading-item-two {
    margin-top: 10px;
}
.topic-loading-item .loading-item-three {
    margin-top: 10px;
}
.dm__tabbar{
  background: #fff;
}
.tabbar-wrap, .navbar-wrap{
  background: #f5f5f5;
  padding: 10px;
}

.dm__navbar--title img{
  height: 30px;
}
.progressView {
  >div {
    margin-bottom: 10px;
  }
}
.flex.dm__flexbox{
  border:1px solid #eee;
  font-size: 16px;
}
.flex.dm__flexbox.dm__flex--vertical{
  border:none;
  height: 300px;
}
.flex.dm__flexbox div:nth-child(odd){
  background: #ededed;
  width: 100px;
  height: 50px;
  align-items: center;
  justify-content: center;
  display: flex;
}

.flex.dm__flexbox.dm__flex--vertical div{
  width: 100%;
}
.flex.dm__flexbox.dm__flex--vertical .dm__flex--item{
  background: #ddd;
  align-items: center;
  justify-content: center;
  display: flex;
}
.dm__seamlessscroll--item{
  font-size: 1.5rem;
}
.selectSign{
  margin-bottom: 0;
}
.selectSign .dm__cell--item{
  padding: 5px 0 5px 10px;
}
.selectSign .dm__cell--left{
    flex: 1!important;
    justify-content: center!important;
}
.selectSign .dm__cell--left .img{
    width: 50px;
}
.selectSign .dm__cell--right{
    flex: 4!important;
    justify-content: flex-start!important;
    text-align: left!important;
}
.selectSign .dm__cell--right p{
    display: flex;
    align-items: center;
    margin: 1rem 0;
}
.selectSign .dm__cell--right p:last-child{
    color: #999;
    font-size: 1.4rem;
}
.selectSign .dm__cell--right p .dm__tag{
    padding:3px 4px;
    font-size: 1.1rem!important;
    margin-left: .5rem;
}
.goTopView {
  position: fixed;
  right: 10px;
  bottom: 10px;
  z-index: 999999;
  cursor: pointer;
  .dm__icon--totop {
    font-size: 3.5rem;
  }
}
.dm-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
}

.dm-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
}
  
.dm-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
}
.dmDatePickerView {
  width: 17%;
  height: 40px;
  border-radius: 4px;
  padding-left: 40px;
  margin: 10px auto;
  line-height: 40px;
  position: relative;
  min-width: 220px;
  .iconfont {
    position: absolute;
    left: 5px;
    top: 1px;
    color: #c0c4cc;
  }
  .dm-range-editor  {
    border: none;
    width: 220px;
  }
}
</style>




